import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			selectIndex: 0,
			buttonList: ['发布动态', '发布活动', '发布招募', '发布交易'],
			swiperList: [{
				id: 0,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84000.jpg'
			}, {
				id: 1,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big37006.jpg',
			}, {
				id: 2,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big39000.jpg'
			}, {
				id: 3,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			}, {
				id: 4,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big25011.jpg'
			}, {
				id: 5,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big21016.jpg'
			}, {
				id: 6,
				type: 'image',
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg'
			}],
			showToast: false,
			toastContent: ''
		}
	},
	computed: {
		...mapState({
			user: state => state.AuthUser,
		}),
	},
	methods: {
		hideModal(e) {
			this.showToast = false;
		},
		buttonSelect(e) {
			this.selectIndex = e.currentTarget.dataset.id;
			switch (this.selectIndex) {
				case 0:
					uni.navigateTo({
						url: '/pages/addition-article/addition-article'
					});
					break;
				case 1:
					if (this.user.user_organization == '') {
						this.toastContent = '只有学生组织才可发布活动，快去认证吧'
						this.showToast = true;
						return;
					}
					uni.navigateTo({
						url: '/pages/addition-activity/addition-activity'
					});
					break;
				case 3:
					uni.navigateTo({
						url: '/pages/addition-commodity/addition-commodity'
					});
					break;
			}
		}
	}
}
